'use client'

import { useState } from 'react'
import { Header } from '@/components/header'
import { Footer } from '@/components/footer'
import { Landing } from '@/components/landing'
import { Upload } from '@/components/upload'
import { Personalization } from '@/components/personalization'
import { Processing } from '@/components/processing'
import { Player } from '@/components/player'
import { Library } from '@/components/library'
import { Pricing } from '@/components/pricing'
import { Contact } from '@/components/contact'
import { Login } from '@/components/login'
import { Signup } from '@/components/signup'

type Screen = 'landing' | 'upload' | 'personalization' | 'processing' | 'player' | 'library' | 'pricing' | 'contact' | 'login' | 'signup'

export default function Home() {
  const [screen, setScreen] = useState<Screen>('landing')
  const [videoGenerated, setVideoGenerated] = useState(false)

  const handleStartLearning = () => setScreen('upload')
  const handleUploadNext = () => setScreen('personalization')
  const handlePersonalizationNext = () => setScreen('processing')
  const handleProcessingComplete = () => {
    setVideoGenerated(true)
    setScreen('player')
  }
  const handleCreateAnother = () => {
    setVideoGenerated(false)
    setScreen('upload')
  }

  const handleLogoClick = () => {
    setVideoGenerated(false)
    setScreen('landing')
  }

  const handleNavigateToLibrary = () => setScreen('library')
  const handleNavigateToPricing = () => setScreen('pricing')
  const handleNavigateToContact = () => setScreen('contact')
  const handleNavigateToLogin = () => setScreen('login')
  const handleNavigateToSignup = () => setScreen('signup')
  const handleAuthSuccess = () => setScreen('landing')

  return (
    <div className="min-h-screen bg-background flex flex-col">
      {screen !== 'login' && screen !== 'signup' && (
        <Header 
          onLogoClick={handleLogoClick} 
          onNavigateToLibrary={handleNavigateToLibrary}
          onNavigateToPricing={handleNavigateToPricing}
          onNavigateToContact={handleNavigateToContact}
          onNavigateToLogin={handleNavigateToLogin}
          onNavigateToSignup={handleNavigateToSignup}
          showLogo={screen !== 'login' && screen !== 'signup'}
        />
      )}

      <main className="flex-grow">
        {screen === 'landing' && <Landing onStart={handleStartLearning} />}
        {screen === 'upload' && <Upload onNext={handleUploadNext} />}
        {screen === 'personalization' && <Personalization onNext={handlePersonalizationNext} />}
        {screen === 'processing' && <Processing onComplete={handleProcessingComplete} />}
        {screen === 'player' && <Player onCreateAnother={handleCreateAnother} />}
        {screen === 'library' && <Library />}
        {screen === 'pricing' && <Pricing />}
        {screen === 'contact' && <Contact />}
        {screen === 'login' && <Login onSuccess={handleAuthSuccess} onSwitchToSignup={handleNavigateToSignup} onHome={handleLogoClick} />}
        {screen === 'signup' && <Signup onSuccess={handleAuthSuccess} onSwitchToLogin={handleNavigateToLogin} onHome={handleLogoClick} />}
      </main>

      {screen !== 'login' && screen !== 'signup' && <Footer />}
    </div>
  )
}
