'use client'

import { Play, Share2, Share, MessageCircle, Copy, Check } from 'lucide-react'
import { useState } from 'react'
import { motion } from 'framer-motion'

interface PlayerProps {
  onCreateAnother: () => void
}

export function Player({ onCreateAnother }: PlayerProps) {
  const [isPlaying, setIsPlaying] = useState(false)
  const [copied, setCopied] = useState(false)
  const [feedback, setFeedback] = useState<'up' | 'down' | null>(null)
  const [showShareMenu, setShowShareMenu] = useState(false)

  const handleCopy = () => {
    navigator.clipboard.writeText(window.location.href)
    setCopied(true)
    setTimeout(() => setCopied(false), 2000)
  }

  return (
    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.6 }}
      className="min-h-screen flex flex-col items-center justify-center px-4 py-12"
    >
      <div className="w-full max-w-4xl">
        {/* Video Player */}
        <motion.div
          initial={{ opacity: 0, scale: 0.95 }}
          animate={{ opacity: 1, scale: 1 }}
          transition={{ duration: 0.6 }}
          className="mb-8"
        >
          <div className="relative w-full aspect-video bg-card border border-border rounded-2xl overflow-hidden group cursor-pointer">
            <div className="absolute inset-0 bg-gradient-to-b from-primary/20 to-secondary/20 flex items-center justify-center">
              <motion.button
                whileHover={{ scale: 1.1 }}
                whileTap={{ scale: 0.95 }}
                onClick={() => setIsPlaying(!isPlaying)}
                className="p-6 bg-primary text-primary-foreground rounded-full hover:shadow-2xl transition-smooth"
              >
                <Play className="w-10 h-10 fill-current" />
              </motion.button>
            </div>

            {/* Video controls */}
            <div className="absolute bottom-0 left-0 right-0 bg-gradient-to-t from-black/60 to-transparent p-4 flex items-center gap-3 opacity-0 group-hover:opacity-100 transition-opacity">
              <div className="flex-1 h-1 bg-white/20 rounded-full">
                <motion.div className="h-full w-1/3 bg-primary rounded-full" />
              </div>
              <span className="text-white text-sm">0:45 / 1:00</span>
            </div>
          </div>
        </motion.div>

        {/* Title */}
        <motion.h1
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6, delay: 0.1 }}
          className="text-3xl md:text-4xl font-bold text-foreground mb-8 text-center"
        >
          Your Personalized Lesson
        </motion.h1>

        {/* Action Buttons */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6, delay: 0.2 }}
          className="flex flex-col md:flex-row gap-4 mb-12 justify-center"
        >
          {/* Create Another */}
          <button
            onClick={onCreateAnother}
            className="px-8 py-3 bg-primary text-primary-foreground font-semibold rounded-xl hover-lift shadow-lg"
          >
            Create Another
          </button>

          {/* Share Button */}
          <div className="relative">
            <button
              onClick={() => setShowShareMenu(!showShareMenu)}
              className="px-8 py-3 border border-primary text-primary font-semibold rounded-xl hover:bg-primary/10 transition-smooth flex items-center gap-2"
            >
              <Share2 className="w-5 h-5" />
              Share
            </button>

            {showShareMenu && (
              <motion.div
                initial={{ opacity: 0, y: -10 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.2 }}
                className="absolute top-full mt-2 left-1/2 -translate-x-1/2 bg-card border border-border rounded-xl shadow-xl overflow-hidden z-50 w-48"
              >
                {[
                  { icon: Copy, label: 'Copy Link', action: handleCopy },
                  { icon: Share, label: 'Twitter', action: () => {} },
                  { icon: Share, label: 'Instagram', action: () => {} },
                ].map((item, i) => (
                  <button
                    key={i}
                    onClick={item.action}
                    className="w-full px-4 py-3 flex items-center gap-3 hover:bg-primary/10 transition-smooth text-left text-foreground border-b border-border last:border-0"
                  >
                    <item.icon className="w-4 h-4" />
                    {item.label}
                    {item.label === 'Copy Link' && copied && (
                      <Check className="w-4 h-4 ml-auto text-primary" />
                    )}
                  </button>
                ))}
              </motion.div>
            )}
          </div>
        </motion.div>

        {/* Feedback Section */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6, delay: 0.3 }}
          className="p-6 bg-card border border-border rounded-2xl text-center"
        >
          <p className="text-foreground font-medium mb-4 flex items-center justify-center gap-2">
            <MessageCircle className="w-5 h-5" />
            Was this helpful?
          </p>
          <div className="flex justify-center gap-4">
            {[
              { emoji: 'ðŸ‘', value: 'up' as const },
              { emoji: 'ðŸ‘Ž', value: 'down' as const },
            ].map(({ emoji, value }) => (
              <motion.button
                key={value}
                whileHover={{ scale: 1.1 }}
                whileTap={{ scale: 0.95 }}
                onClick={() => setFeedback(value)}
                className={`text-4xl p-3 rounded-lg transition-smooth ${
                  feedback === value ? 'bg-primary/20' : 'hover:bg-primary/10'
                }`}
              >
                {emoji}
              </motion.button>
            ))}
          </div>
        </motion.div>
      </div>
    </motion.div>
  )
}
