'use client'

import { motion } from 'framer-motion'

interface HeaderProps {
  onLogoClick: () => void
  onNavigateToLibrary?: () => void
  onNavigateToPricing?: () => void
  onNavigateToContact?: () => void
  onNavigateToLogin?: () => void
  onNavigateToSignup?: () => void
  showLogo?: boolean
}

export function Header({ 
  onLogoClick, 
  onNavigateToLibrary, 
  onNavigateToPricing,
  onNavigateToContact,
  onNavigateToLogin,
  onNavigateToSignup,
  showLogo = true
}: HeaderProps) {
  const navItems = [
    { label: 'Home', action: onLogoClick },
    { label: 'Library', action: onNavigateToLibrary },
    { label: 'Pricing', action: onNavigateToPricing },
    { label: 'Contact', action: onNavigateToContact }
  ]

  return (
    <motion.header
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.6 }}
      className="fixed top-0 left-0 right-0 z-50 bg-background/80 backdrop-blur-xl border-b border-border/20"
    >
      <div className="max-w-7xl mx-auto px-6 lg:px-8 h-16 flex items-center justify-between">
        {/* Logo - Left */}
        {showLogo && (
          <button
            onClick={onLogoClick}
            className="group flex items-center gap-2 cursor-pointer transition-smooth hover:opacity-80"
          >
            <div className="w-8 h-8 rounded-full bg-gradient-to-br from-primary to-accent flex items-center justify-center">
              <span className="text-xs font-bold text-primary-foreground">R</span>
            </div>
            <span className="font-bold text-lg bg-gradient-to-r from-primary to-accent bg-clip-text text-transparent">
              Rusaldo
            </span>
          </button>
        )}
        {!showLogo && <div className="w-32" />}

        {/* Navigation - Center */}
        <nav className="hidden md:flex items-center gap-12">
          {navItems.map((item) => (
            <button
              key={item.label}
              onClick={item.action}
              className="text-sm text-muted-foreground hover:text-foreground transition-smooth cursor-pointer"
            >
              {item.label}
            </button>
          ))}
        </nav>

        {/* Auth - Right */}
        <div className="flex items-center gap-4">
          <button 
            onClick={onNavigateToLogin}
            className="text-sm text-muted-foreground hover:text-foreground transition-smooth cursor-pointer"
          >
            Log in
          </button>
          <button 
            onClick={onNavigateToSignup}
            className="px-6 py-2 bg-primary text-primary-foreground text-sm font-medium rounded-full hover-lift cursor-pointer"
          >
            Sign up
          </button>
        </div>
      </div>
    </motion.header>
  )
}
